/************************************************************/
/*    Common methods for regression models                  */
/************************************************************/

import { seq } from './vectors.js';
import { Matrix, dot, ncol, nrow, _dot, tcrossprod, zeros } from './Matrix.js';
import { mean, ssq } from './stat.js';
import { scale as prep_scale, unscale as prep_unscale } from './prep.js';


/**
 * Split matrix with data values into matrix of predictors (X) and and matrix with responses (Y).
 *
 * @param {Matrix} data - matrix with data values.
 *
 * @returns {Array} array with X and Y.
 */
export function splitregdata(data) {
   const nrows = nrow(data);
   const ncols = ncol(data);

   return [
      new Matrix(data.v.subarray(nrows), nrows, ncols - 1),
      new Matrix(data.getcolumn(1), nrows, 1)
   ];
}

/**
 * Make predictions based on x-scores and y-loadings (for PCR or PLS1) and compute
 * performance statistics if reference y-values are provided.
 *
 * @param {Matrix} T - matrix with X-scores.
 * @param {Matrix} C - matrix with y-loadings.
 * @param {number} mY - number to use for centering y-values (from the model).
 * @param {number} sY - number to use for scaling y-values (from the model).
 * @param {Float64Array} [y] - vector with reference response values.
 */
export function reggetmainres(T, C, mY, sY, Yref) {

   const nrows = nrow(T);
   const ncomp = ncol(T);

   // prepare variables for predictions and statistics
   const Ypred = zeros(nrows, ncomp);

   // if no reference values, compute predictions and return
   if (Yref === undefined || Yref === null || Yref === []) {
      for (let a = 1; a <= ncomp; a++) {
         // compute predictions
         const cind = seq(1, a);
         const Ypreda = prep_unscale(dot(T.subset([], cind), C.subset([], cind)), my, sy);
         Ypred.v.set(Ypreda.v, (a - 1) * nrows);
      }

      return {"Ypred": Ypred};
   }

   // otherwise compute statistics as well
   const rmse = new Float64Array(ncomp);
   const r2 = new Float64Array(ncomp);
   const bias = new Float64Array(ncomp);

   // total y-variance
   const ssy = ssq(prep_scale(Yref, mY).v);

   for (let a = 1; a <= ncomp; a++) {

      // compute predictions
      const cind = seq(1, a);
      const Ypreda = prep_unscale(tcrossprod(T.subset([], cind), C.subset([], cind)), mY, sY);
      Ypred.v.set(Ypreda.v, (a - 1) * nrows);

      // compute performance statistics
      const e = Yref.subtract(Ypreda);
      const sse = ssq(e.v);
      bias[a - 1] = mean(e.v);
      rmse[a - 1] = Math.sqrt(sse / nrows);
      r2[a - 1] = 1 - sse / ssy;
   }

   return {
      "Yref": Yref,
      "Ypred": Ypred,
      "rmse": rmse,
      "r2": r2,
      "bias": bias
   };
}

