<script>
   import { ncol, ismatrix, nrow } from '../mdatools/Matrix.js';
   import { rep, repeach, seq } from '../mdatools/vectors.js';
   import Rectangles from '../../../svelte-plots-basic/src/Rectangles.svelte';
   import Axes from '../../../svelte-plots-basic/src/Axes.svelte';
   import Box from '../../../svelte-plots-basic/src/Box.svelte';
   import XAxis from '../../../svelte-plots-basic/src/XAxis.svelte';
   import YAxis from '../../../svelte-plots-basic/src/YAxis.svelte';

   // input parameters
	export let data;
   export let bins;
   export let colormap;
   export let title;
   export let xLabel;
   export let yLabel;


   function getColorVector(data) {

      if (!ismatrix(data)) {
         throw Error("Heatmap: parameter 'data' must be a matrix.");
      }

      if (colormap.length !== bins.length - 1) {
         throw Error("Heatmap: number of columns in 'colormap' must be the same as number of intervals in 'bins'.");
      }

      const nrows = nrow(data);
      const ncols = ncol(data);

      const height = 1;
      const width = 1;
      const left = repeach(seq(0.5, ncols - 0.5, 1, false), nrows);
      const top = rep(seq(1.5, nrows + 0.5, 1, false), ncols);

      const nbins = bins.length - 1;
      const leftv = new Array(nbins);
      const topv = new Array(nbins);
      for (let i = 1; i <= nbins; i++) {
         leftv[i - 1] = left.filter((v, k) => data.v[k] > bins[i - 1] && data.v[k] <= bins[i]);
         topv[i - 1]  =  top.filter((v, k) => data.v[k] > bins[i - 1] && data.v[k] <= bins[i]);
      }

      return [leftv, topv, width, height];
   }

   // compute coordinates for each bin
   $: [leftv, topv, width, height] = getColorVector(data);

   // compute limits
   $: limY = [0.5, nrow(data) + 0.5];
   $: limX = [0.5, ncol(data) + 0.5];
</script>

<Axes {limX} {limY} {title} {xLabel} {yLabel} >

   {#if leftv && leftv.length > 0}
      {#each leftv as l, i}
         {#if l && leftv[i].length > 0}
         <Rectangles left={leftv[i]} top={topv[i]} height={height} width={width} faceColor={colormap[i]} />
         {/if}
      {/each}
   {/if}

   <XAxis slot="xaxis" />
   <YAxis slot="yaxis" />
   <Box slot="box" />
</Axes>