<script>
   import { createEventDispatcher } from "svelte";

   export let id;
   export let value;
   export let min;
   export let max;
   export let decNum = 1;
   export let step = +((max - min) / 100).toFixed(4);
   if (value < min || value > max) {
      throw("The value is outside of the provided range.");
   }

   const dispatch = createEventDispatcher();

   let sliderElement;
   let sliderContainer;
   let isDragging = false;

   const computeValue = (p) => {
      const tmpValue = min + p * (max - min);

      // strange construction below is needed for:
      // a. make a value fractionated according to step
      // b. get rid of small decimals added by JS due to loss of precision
      return(+(Math.round(tmpValue / step) * step).toFixed(4));
   }

   const getRelativePosition = (e) => {
      const sliderRect = sliderElement.getBoundingClientRect();
      const parentRect = sliderContainer.getBoundingClientRect();
      const minX = sliderRect.x;
      const maxX = parentRect.x + parentRect.width;

      return (e.clientX - minX) / (maxX - minX);
   }

   const startChanging = (e) => {
      const p = getRelativePosition(e);
      if (p < 0 || p > 1) return;
      isDragging = p * 100 > width - 5 && p * 100 < width + 5;
   }

   const cancelChanging = (e) => {
      isDragging = false;
   }

   const stopChanging = (e) => {
      isDragging = false;
      const p = getRelativePosition(e);
      if (p < 0 || p > 1) return;

      value = computeValue(p);
   }

   const changingByWheel = (e) => {
      let newValue = value + step * e.deltaY * 0.5;
      if (newValue < min) newValue = min;
      if (newValue > max) newValue = max;
      value = +(Math.round(newValue / step) * step).toFixed(4);
   }

   const changing = (e) => {
      if (!isDragging) return;
      const p = getRelativePosition(e);
      if (p < 0 || p > 1) return;

      value = computeValue(p);
   }

   $: width = (value - min) / (max - min) * 100;
   $: dispatch("change", value);
</script>

<div
   class="range-slider-container"
   bind:this={sliderContainer}
   on:mousewheel|preventDefault={changingByWheel}
   on:mousemove={changing}
   on:mousedown={startChanging}
   on:mouseleave={cancelChanging}
   on:mouseup={stopChanging}>

   <div class="range-slider" style="width:{width}%" bind:this={sliderElement}></div>
</div>
<span class="range-value">{value.toFixed(decNum)}</span>
<input type="range" name={id} step={step} bind:value={value} min={min} max={max}>

<style>
   .range-slider-container {
      position: relative;
      box-sizing: border-box;
      display: inline-block;
      flex: 1 1 auto;
      background: #f0f0f0;
      height: 1.5em;
      margin: 0;
      padding: 1px 0;
      width: auto;
      border-radius: 2px;

      user-select: none;
      -webkit-user-select: none;
      -moz-user-select: none;

   }

   .range-slider {
      position: relative;
      display: inline-block;
      box-sizing: border-box;
      background: #6EB8FF;
      border-radius: 2px;
      text-align: right;
      color: #fafafa;
      margin: 0;
      padding: 0;
      height: 100%;
      cursor:default;
      border-right: 5px solid #528abe;
      user-select: none;
      -webkit-user-select: none;
      -moz-user-select: none;
   }

   .range-value {
      display: inline-block;
      right: 0;
      top: 0;
      font-size: 0.85em;
      padding: 1px 0em 1px 0.5em;
      color: #606060;
      line-height: 1.6em;
      user-select: none;
      -webkit-user-select: none;
      -moz-user-select: none;
   }

   input {
      display: none;
      width: 100%;
   }
</style>

