/**
 *    Matrix with 'nr' rows and 'nc' columns is defined as an Array with two elements:
 *    [Float64Array(nr * nc), dim::[nr, nc]]
 *
 *    Indices in loops, subsets, etc:
 *    - in loops and all functions indices are specified in normal way (start from 1)
 *    - when access arrays we always subtract 1 (as in JS indices start from 0)
 *
 *    Examples:
 *    - Let X be a matrix with nr = 10 and nc = 5
 *    - element at row 'r' and column 'c' (t = false) -> X.v[(c - 1) * nr + r - 1]
 *    - element at row 'r' and column 'c' (t = true)  -> X.v[(c - 1) * nr + r - 1]
 *
 */



import { seq } from './vectors.js';
import { min, max, sum } from './stat.js';


// tolerance for small values
const TOLERANCE = Math.pow(10, -9);

// check that object is a matrix
export function ismatrix(X) {
   return X.constructor === Matrix;
}


/*************************************************/
/*  Methods for operations between two matrices  */
/*************************************************/

export function cbind(X, Y) {

   const ncx = ncol(X);
   const nrx = nrow(X);

   const ncy = ncol(Y);
   const nry = nrow(Y);

   if (nrx !== nry) {
      throw Error("cbind: the matrices have different number of rows.");
   }

   const out = zeros(nrx, ncx + ncy);
   out.v.set(X.v, 0);
   out.v.set(Y.v, nrx * ncx);

   return out;
}

/**
 * Compute XY' product of two matrices.
 *
 * @param {Matrix} X - a matrix.
 * @param {Matrix} Y - a matrix.
 *
 * @returns {Matrix} result of the product.
 *
 */
export function tcrossprod(X, Y) {

   if (Y === undefined) {
      Y = X;
   }

   const nrx = nrow(X);
   const ncx = ncol(X);

   const nry = nrow(Y);
   const ncy = ncol(Y);

   // create vector for the product
   const nrows = nrx;
   const ncols = nry;
   const ninner = ncx;
   const out = new Float64Array(nrows * ncols);

   for (let i = 0; i < ninner; i++) {
      const xr = X.v.subarray(i * nrx, (i + 1) * nrx);
      const yc = Y.v.subarray(i * nry, (i + 1) * nry);

      for (let c = 0; c < ncols; c++) {
         const outc = out.subarray(c * nrows, (c + 1) * nrows);
         for (let r = 0; r < nrows; r++) {
            outc[r] += xr[r] * yc[c];
         }
      }
   }
   return new Matrix(out, nrows, ncols);

}


/**
 * Compute X'Y product of two matrices.
 *
 * @param {Matrix} X - a matrix.
 * @param {Matrix} Y - a matrix.
 *
 * @returns {Matrix} result of the product.
 *
 */
export function crossprod(X, Y) {

   if (Y === undefined) {
      Y = X;
   }

   const nrx = nrow(X);
   const ncx = ncol(X);

   const nry = nrow(Y);
   const ncy = ncol(Y);

   // create vector for the product
   const nrows = ncx;
   const ncols = ncy;
   const ninner = nrx;
   const out = new Float64Array(nrows * ncols);

   for (let c = 0; c < ncols; c++) {
      // column c of Y
      const yc = Y.v.subarray(c * nry, (c + 1) * nry);

      // column c of out
      const outc = out.subarray(c * nrows, (c + 1) * nrows);

      for (let r = 0; r < nrows; r++) {

         // column r of X (row r of X')
         const xr = X.v.subarray(r * nrx, (r + 1) * nrx);

         let s = 0;
         for (let i = 0; i < ninner; i++) {
            s += xr[i] * yc[i];
         }
         outc[r] = s;
      }
   }

   return new Matrix(out, nrows, ncols);
}


/**
 * Generic function for taking a dot product of two matrices, X and Y, represented as vectors
 * @param {Float64Array} xv - vector with values from X
 * @param {Float64Array} yv - vector with values from Y
 * @param {number} nrx - number of rows in X
 * @param {number} ncx - number of columns in X
 * @param {number} nry - number of rows in Y
 * @param {number} ncy - number of columns in Y
 *
 * @returns {Float64Array} - vector with result of the product
 */
export function _dot(xv, yv, nrx, ncx, nry, ncy) {

   if (ncx !== nry) {
      throw Error('Matrix dimensions do not much.');
   }

   // create vector for the product
   const nrows = nrx;
   const ncols = ncy;
   const ninner = ncx;
   const out = new Float64Array(nrows * ncols);

   for (let c = 0; c < ncols; c++) {
      const yc = yv.subarray(c * nry, (c + 1) * nry)
      const outc = out.subarray(c * nrows, (c + 1) * nrows);

      for (let i = 0; i < ninner; i++) {
         const yci = yc[i];
         const xr = xv.subarray(i * nrx, (i + 1) * nrx)
         for (let r = 0; r < nrows; r++) {
            outc[r] += xr[r] * yci;
         }
      }
   }

   return out;
}


/**
 * Take a dot product of a matrix with a vector.
 * @param {Matrix} X - a matrix.
 * @param {Float64Array} y - a vector.
 *
 * @returns {Matrix} - result of the dot product
 */
export function dotv(X, y) {
   return new Matrix(_dot(X.v, y, nrow(X), ncol(X), y.length, 1), nrow(X), 1);
}


/**
 * Compute dot product of X and Y.
 *
 * @param {Matrix} X - a matrix.
 * @param {Matrix} Y - a matrix.
 *
 * @return {Matrix} result of dot product.
 *
 */
export function dot(X, Y) {
   return new Matrix(_dot(X.v, Y.v, nrow(X), ncol(X), nrow(Y), ncol(Y)), nrow(X), ncol(Y))
}


/************************************************************/
/*    Methods for getting matrix properties and statistics  */
/************************************************************/


/**
 * Check if matrix is lower triangular.
 *
 * @param {Matrix} X - a matrix.
 *
 * @returns {boolean} true if matrix is lower triangular, false otherwise.
 */
export function islowertriangular(X) {

   const nrows = nrow(X);
   const ncols = ncol(X);

   if (nrows !== ncols) return false;
   for (let j = 1; j < ncols; j++) {
      for (let i = 0; i <= j - 1; i++) {
         if (Math.abs(X.v[j * nrows + i]) > TOLERANCE) return false;
      }
   }

   return true;
}


/**
 * Check if matrix is upper triangular.
 *
 * @param {Matrix} X - a matrix.
 *
 * @returns {boolean} true if matrix is upper triangular, false otherwise.
 */
export function isuppertriangular(X) {

   const nrows = nrow(X);
   const ncols = ncol(X);

   if (nrows !== ncols) return false;

   for (let j = 0; j <= ncols - 1; j++) {
      for (let i = j + 1; i < nrows; i++) {
         if (Math.abs(X.v[j * nrows + i]) > TOLERANCE) return false;
      }
   }

   return true;
}


/**
 * Return sum of every column of a matrix.
 *
 * @param {Matrix} X - a matrix.
 *
 * @returns {Float32Array} vector with sums.
 *
 */
export function colSums(X) {
   return X.apply(sum, 2)
}


/**
 * Return sum of every row of a matrix.
 *
 * @param {Matrix} X - a matrix.
 *
 * @returns {Float32Array} vector with sums.
 *
 */
export function rowSums(X) {
   return X.apply(sum, 1)
}


/**
 * Return vector with matrix dimension.
 *
 * @param {Matrix} X - a matrix.
 *
 * @returns {number[]} a vector with number of rows and columns.
 *
 */
export function dim(X) {
   return X.dim;
}


/**
 * Return number of rows in matrix.
 *
 * @param {Matrix} X - a matrix.
 *
 * @returns {number} number of rows.
 *
 */
export function nrow(X) {
   return X.dim[0];
}


/**
 * Return number of columns in matrix.
 *
 * @param {Matrix} X - a matrix.
 *
 * @returns {number} number of columns.
 *
 */
export function ncol(X) {
   return X.dim[1];
}


/**
 * Return vector with diagonal elements of matrix.
 *
 * @param {Matrix} X - a matrix.
 *
 * @returns {Float64Array} vector with diagonal elements.
 *
 */
export function diag(X) {

   const n = Math.min(nrow(X), ncol(X));
   const out = new Float64Array(n);
   for (let rc = 0; rc < n; rc++) {
      out[rc] = X.getcolumn(rc + 1)[rc];
   }

   return out;
}



/*************************************************/
/*    Methods for creating matrices              */
/*************************************************/


/**
 * Create a diagonal matrix based on vector with values.
 *
 * @param {number[] | Float64Array} x - vector with values.
 *
 * @returns {Matrix} the matrix.
 *
 * @see {@link Matrix}
 *
 */
export function diagm(x) {

   const n = x.length;
   const X = new Float64Array(n * n);
   for (let rc = 1; rc <= n; rc++) {
      X[(rc - 1) * n + rc - 1] = x[rc - 1];
   }

   return new Matrix(X, n, n);
}


/**
 * Create a matrix filled with random values from U[0, 1].
 *
 * @param {number} nrows - number of rows in matrix.
 * @param {number} ncols - number of columns in matrix.
 *
 * @returns {Matrix} the matrix.
 *
 * @see {@link Matrix}
 *
 */
export function rand(nrows, ncols, range) {
   if (range === undefined) {
      range = [0, 1];
   }

   const dr = range[1] - range[0];
   const r0 = range[0];

   let X = new Float64Array(nrows * ncols);
   for (let i = 1; i <= nrows * ncols; i++) {
      X[i - 1] = Math.random() * dr + r0;
   }

   return new Matrix(X, nrows, ncols);
}


/**
 * Create a matrix filled with random values from N[0, 1].
 *
 * @param {number} nrows - number of rows in matrix.
 * @param {number} ncols - number of columns in matrix.
 *
 * @returns {Matrix} the matrix.
 *
 * @see {@link Matrix}
 *
 */
export function randn(nrows, ncols) {

   function r() {
      const u1 = Math.random();
      const u2 = Math.random();
      return Math.sqrt(-2.0 * Math.log(u1)) * Math.cos(2.0 * Math.PI * u2);
   }

   let X = new Float64Array(nrows * ncols);
   for (let i = 1; i <= nrows * ncols; i++) {
      X[i - 1] = r();
   }

   return new Matrix(X, nrows, ncols);
}


/**
 * Create an identity matrix.
 *
 * @param {number} nrows - number of rows in matrix.
 * @param {number} ncols - number of columns in matrix.
 *
 * @returns {Matrix} the matrix.
 *
 * @see {@link Matrix}
 *
 */
export function eye(nrows, ncols) {

   if (ncols === undefined) {
      ncols = nrows;
   }

   const X = new Float64Array(nrows * ncols);
   for (let rc = 1; rc <= Math.min(nrows, ncols); rc++) {
      X[(rc - 1) * nrows + rc - 1] = 1;
   }

   return new Matrix(X, nrows, ncols);
}


/**
 * Create a Matrix object filled with zeros.
 *
 * @param {number} nrows - number of rows in matrix.
 * @param {number} ncols - number of columns in matrix.
 *
 * @returns {Matrix} the matrix.
 *
 * @see {@link Matrix}
 *
 */
export function zeros(nrows, ncols) {
   if (ncols === undefined) ncols = nrows;
   return new Matrix(new Float64Array(nrows * ncols), nrows, ncols);
}


/**
 * Create a Matrix object from array of values.
 *
 * @param {number[]} values - array with values.
 * @param {number} nrows - number of rows in matrix.
 * @param {number} ncols - number of columns in matrix.
 *
 * @returns {Matrix} the matrix.
 *
 * @see {@link Matrix}
 *
 */
export function matrix(values, nrows, ncols) {
   return new Matrix(new Float64Array(values), nrows, ncols);
}



/*************************************************/
/*    Matrix constructor and class methods       */
/*************************************************/


/**
 * Create a Matrix object.
 *
 * @param {Float64Array} values - typed array with values.
 * @param {number} nrows - number of rows in matrix.
 * @param {number} ncols - number of columns in matrix.
 *
 * @returns {Matrix} a Matrix object (see description).
 * @constructor
 *
 * @description  Create a matrix from vector of values. Matrix is represented by an object with two
 * fields. Field 'v' is a typed Float64Array with all values, unfolded column by column. The second
 * field, 'dim', is an array with 2 values: number of rows and number of columns.
 *
 */
export function Matrix(values, nrows, ncols) {

   if (!ArrayBuffer.isView(values) || values.constructor !== Float64Array) {
      throw Error('Matrix: parameter "values" must be Float64Array.')
   }

   if (values.length != nrows * ncols) {
      throw Error('Matrix: number elements in "values" is not equal to "nrows" x "ncols".')
   }

   this.v = values;
   this.dim = [nrows, ncols];
}


/**
 * Applies function to elements of a matrix.
 * @param {function} fun - function to apply (should return single value).
 * @param {number} dims - which element to apply the function to (0 - every, 1 - row, 2 - column).
 *
 * @returns {Array|Matrix} - result of applying the function (either array with one value for each
 * row/column or another Matrix with transformed values).
 */
Matrix.prototype.apply = function(fun, dims) {

   // by default function is applied to columns
   if (dims === undefined) {
      dims = 2;
   }

   // columns
   if (dims == 2) {
      const n = ncol(this)
      const out = new Float64Array(n);
      for (let i = 1; i <= n; i++) {
         out[i - 1] = fun(this.getcolumn(i));
      }
      return out;
   }

   // rows
   if (dims == 1) {
      return this.t().apply(fun, 2);
   }

   // individual values
   if (dims == 0) {
      const n = this.v.length;
      const out = new Float64Array(n);
      for (let i = 0; i < n; i++) {
         out[i] = fun(this.v[i]);
      }
      return new Matrix(out, nrow(this), ncol(this));
   }
}


/**
 * Arithmetic operation between elements of the matrix and a vector of values.
 *
 * @param {Float64Array} v - vector with values.
 * @param {function} op - function which implements an operation between two numbers.
 *
 * @returns {Matrix} the result of the operation.
 *
 * @description If vector 'v' has the same number of elements as number of rows, it will be
 * applied to every column of the matrix. If it has the same number of elements as number
 * of columns - it will be applied to every row. Otherwise it is expected to have the same
 * number of elements as the matrix.
 *
 */
Matrix.prototype.opv = function(v, op, dim) {

   const nrows = nrow(this);
   const ncols = ncol(this);

   const l = nrows * ncols;

   // vector has the same number of values as number of rows - apply to every column
   if (dim === 1 || (dim === undefined && v.length === nrows)) {

      if (v.length !== nrows) {
         throw Error("Matrix.opv: length of vector does not equal to number of rows in the matrix.");
      }

      const X = zeros(nrows, ncols);
      for (let c = 1; c <= ncols; c++) {
         const vc = this.getcolumn(c);
         const xc = X.getcolumn(c);
         for (let r = 1; r <= nrows; r++) {
            xc[r - 1] = op(vc[r - 1], v[r - 1]);
         }
      }

      return X;
   }

   // vector has the same number of values as number of columns - apply to every row
   if (dim === 2 || (dim === undefined && v.length === ncols)) {

      if (v.length !== ncols) {
         throw Error("Matrix.opv: length of vector does not equal to number of columns in the matrix.");
      }

      const X = zeros(nrows, ncols);
      for (let c = 1; c <= ncols; c++) {
         const vc = this.getcolumn(c);
         const xc = X.getcolumn(c);
         for (let r = 1; r <= nrows; r++) {
            xc[r - 1] = op(vc[r - 1], v[c - 1]);
         }
      }

      return X;
   }

   if (v.length !== l) {
      throw Error('Matrix.opv: vector length should match number of rows, number of columns or number of elements in matrix.');
   }

   const X = new Float64Array(l);
   for (let i = 0; i < l; i++) {
      X[i] = op(this.v[i], v[i]);
   }

   return new Matrix(X, nrows, ncols);

}


/**
 * Arithmetic operation between elements of the matrix and a number.
 *
 * @param {Float64Array} x - a number.
 * @param {function} op - function which implements an operation between two numbers.
 *
 * @returns {Matrix} the result of the operation.
 *
 */
Matrix.prototype.opn = function(x, op) {
   const l = nrow(this) * ncol(this);
   const X = new Float64Array(l);
   for (let i = 0; i < l; i++) {
      X[i] = op(this.v[i], x);
   }

   return new Matrix(X, nrow(this), ncol(this));
}


/**
 * Element wise arithmetic operation with a vector, a number or another Matrix.
 *
 * @param {number|Float64Array|Matrix} X - a number, a typed array with numbers, or a Matrix.
 * @param {function} op - function which computes a value based on two numbers.
 * @param {number} dim - if X is a vector, which dimension apply it to (1 - rows, 2 - columns).
 *
 * @returns {Matrix} result of operation.
 *
 * @description If 'X' is a vector and has the same number of elements as number of rows, it will
 * be applied to every column of the matrix. If it has the same number of elements as number
 * of columns - it will be applied to every row. If number of rows and columns are equal, then
 * this must be specified by parameter 'dim'.
 *
 * Otherwise it is expected to have the same number of elements as the matrix.
 *
 */
Matrix.prototype.op = function(X, op, dim) {

   // second argument is a number
   if (typeof(X) === 'number') {
      return this.opn(X, op);
   }

   // second argument is Matrix
   if (X.constructor === Matrix) {
      return this.opv(X.v, op);
   }

   // second argument is a vector
   if (ArrayBuffer.isView(X) && X.constructor === Float64Array) {
      return this.opv(X, op, dim);
   }

   throw Error('Matrix: parameter "values" must be Float64Array.')
}


/**
 * Element wise addition with a vector, a number or another Matrix.
 *
 * @param {number|Float64Array|Matrix} X - a number, a typed array with numbers, or a Matrix.
 * @param {number} dim - if X is a vector, which dimension apply it to (1 - rows, 2 - columns).
 *
 * @returns {Matrix} result of addition.
 *
 * @description If 'X' is a vector and has the same number of elements as number of rows, it will
 * be applied to every column of the matrix. If it has the same number of elements as number
 * of columns - it will be applied to every row. Otherwise it is expected to have the same
 * number of elements as the matrix.
 *
 */
Matrix.prototype.add = function(X, dim) {
   return this.op(X, (a, b) => a + b, dim);
}


/**
 * Element wise subtraction of a vector, a number or another Matrix.
 *
 * @param {number|Float64Array|Matrix} X - a number, a typed array with numbers, or a Matrix.
 * @param {number} dim - if X is a vector, which dimension apply it to (1 - rows, 2 - columns).
 *
 * @returns {Matrix} result of subtraction.
 *
 * @description If 'X' is a vector and has the same number of elements as number of rows, it will
 * be applied to every column of the matrix. If it has the same number of elements as number
 * of columns - it will be applied to every row. Otherwise it is expected to have the same
 * number of elements as the matrix.
 *
 */
Matrix.prototype.subtract = function(X, dim) {
   return this.op(X, (a, b) => a - b, dim);
}


/**
 * Element wise multiplication with of a vector, a number or another Matrix.
 *
 * @param {number|Float64Array|Matrix} X - a number, a typed array with numbers, or a Matrix.
 * @param {number} dim - if X is a vector, which dimension apply it to (1 - rows, 2 - columns).
 *
 * @returns {Matrix} result of multiplication.
 *
 * @description If 'X' is a vector and has the same number of elements as number of rows, it will
 * be applied to every column of the matrix. If it has the same number of elements as number
 * of columns - it will be applied to every row. Otherwise it is expected to have the same
 * number of elements as the matrix.
 *
 */
Matrix.prototype.mult = function(X, dim) {
   return this.op(X, (a, b) => a * b, dim);
}


/**
 * Element wise division to of a vector, a number or another Matrix.
 *
 * @param {number|Float64Array|Matrix} X - a number, a typed array with numbers, or a Matrix.
 * @param {number} dim - if X is a vector, which dimension apply it to (1 - rows, 2 - columns).
 *
 * @returns {Matrix} result of multiplication.
 *
 * @description If 'X' is a vector and has the same number of elements as number of rows, it will
 * be applied to every column of the matrix. If it has the same number of elements as number
 * of columns - it will be applied to every row. Otherwise it is expected to have the same
 * number of elements as the matrix.
 *
 */
Matrix.prototype.divide = function(X, dim) {
   return this.op(X, (a, b) => a / b, dim);
}


/**
 * Replace values of matrix specified by 'rind' and 'cind' with values from another matrix.
 *
 * @param {Matrix} - matrix with new values.
 * @param {number | number[] | Int32Array} rind - row indices  (as number or array of values).
 * @param {number | number[] | Int32Array} cind - column indices (as number or array of values).
 *
 * @returns {Matrix} the subset of the matrix.
 *
 * @description Row and column indices must start from 1. Empty array ([]) tells function to use
 * all elements (e.g. all rows or all columns). Size of matrix 'X' should correspond to the
 * number of indices.
 *
 * Function does not create a new matrix but changes the current one.
 *
 */
Matrix.prototype.replace = function(X, rind, cind) {

   if (typeof(rind) === 'number') {
      rind = [rind];
   }

   if (rind.length === 0) {
      rind = seq(1, nrow(this));
   }

   if (typeof(cind) === 'number') {
      cind = [cind];
   }

   if (cind.length === 0) {
      cind = seq(1, ncol(this));
   }

   const nrows = rind.length;
   const ncols = cind.length;
   const nrows_this = nrow(this);
   const ncols_this = ncol(this);

   if (min(rind) < 1 || max(rind) > nrows_this) {
      throw Error('Matrix.replace: row indices do not match the dimension of matrix.');
   }

   if (min(cind) < 1 || max(cind) > ncols_this) {
      throw Error('Matrix.replace: column indices do not match the dimension of matrix.');
   }

   if (nrow(X) !== nrows || ncol(X) !== ncols) {
      throw Error('Matrix.replace: dimension of "X" does not match the indices.');
   }

   for (let ic = 0; ic < cind.length; ic++) {
      const xc = X.getcolumn(ic + 1);
      const cv = (cind[ic] - 1) * nrows_this;
      for (let ir = 0; ir < rind.length; ir++) {
         const r = rind[ir];
         this.v[cv + r - 1] = xc[ir];
      }
   }

   return this;
}


/**
 * Transpose matrix
 *
 * @returns {Matrix} the transposed matrix.
 */
Matrix.prototype.told = function() {

   // swap rows and columns for the new matrix
   const nrowsX = nrow(this);
   const ncolsX = ncol(this);
   const nrowsXt = ncolsX;
   const ncolsXt = nrowsX;
   const x = this.v;

   const out = new Float64Array(x.length);
   const n = Math.min(ncolsXt, nrowsXt);

   for (let i = 0; i < n; i++) {

      out[i * nrowsXt + i] = x[i * nrowsX + i];

      // i is a column of new matrix
      for (let r = i + 1; r < nrowsXt; r++) {
         out[i * nrowsXt + r] = x[r * nrowsX + i];
      }

      // i is a row of new matrix
      for (let c = i + 1; c < ncolsXt; c++) {
         out[c * nrowsXt + i] = x[i * nrowsX + c];
      }
   }


   return new Matrix(out, nrowsXt, ncolsXt);
}


// ! new transposition method
Matrix.prototype.t = function() {

   const n1 = ncol(this);
   const m1 = nrow(this);
   const n2 = m1;
   const m2 = n1;
   const out = new Float64Array(this.v.length);

   if (n1 < m1) {
      for(let i = 0; i < n1; i++) {
         const x = this.v.subarray(i * m1, (i + 1) * m1)
         for(let j = 0; j < m1; j++) {
            out[j * m2 + i] = x[j];
         }
      }

      return new Matrix(out, m2, n2);
   }

   for(let j = 0; j < m1; j++) {
      const outx = out.subarray(j * m2, (j + 1) * m2)
      for(let i = 0; i < n1; i++) {
         outx[i] = this.v[i * m1 + j];
      }
   }

   return new Matrix(out, m2, n2);
}


/**
 * Create a deep copy of the matrix.
 *
 * @returns {Matrix} the copy of the matrix.
 *
*/
Matrix.prototype.copy = function() {
   return new Matrix(this.v.slice(), this.dim[0], this.dim[1]);
}


/**
 * Create a subset of a matrix using row and column indices.
 *
 * @param {number | number[] | Int32Array} rind - row indices to select (as number or array of values).
 * @param {number | number[] | Int32Array} cind - column indices to select (as number or array of values).
 *
 * @returns {Matrix} the subset of the matrix.
 *
 * @description Row and column indices must start from 1. Empty array ([]) tells function to select
 * all elements (e.g. all rows or all columns). If both 'rind' and 'cind' are empty arrays, the
 * function creates a copy of the original matrix.
 *
 * @see {@link Matrix.copy}
 */
Matrix.prototype.subset = function(rind, cind) {

   if (typeof(rind) === 'number') {
      rind = [rind];
   }

   if (typeof(cind) === 'number') {
      cind = [cind];
   }

   const nrows = rind.length;
   const ncols = cind.length;

   // select all rows and all columns
   if (nrows === 0 && ncols === 0) {
      return this.copy();
   }

   // select all rows
   if (nrows === 0) {
      const nrows_new = nrow(this);
      const x = new Float64Array(nrows_new * ncols);
      for (let ic = 0; ic < cind.length; ic++) {
         x.set(this.getcolumn(cind[ic]), ic * nrows_new);
      }
      return new Matrix(x, nrows_new, ncols);
   }

   // select all columns
   if (ncols === 0) {
      return this.t().subset(cind, rind).t();
   }

   // select specific columns and rows
   const X = new Float64Array(nrows * ncols);
   let nxc = new Float64Array(nrows)
   for (let ic = 0; ic < ncols; ic++) {
      const xc = this.getcolumn(cind[ic]);
      for (let ir = 0; ir < nrows; ir++) {
         nxc[ir] = xc[rind[ir] - 1];
      }
      X.set(nxc, ic * nrows);
   }

   return new Matrix(X, nrows, ncols);
}


/**
 * Return a vector with values from particular column of the matrix.
 *
 * @param {number} cind - index of the column (starting from 1).
 *
 * @returns {Float64Array} typed array with column values.
 *
 */
Matrix.prototype.getcolumn = function(cind) {
   return this.v.subarray((cind - 1) * nrow(this), cind * nrow(this));
}


/**
 * Return a vector with values from particular row of the matrix.
 *
 * @param {number} rind - index of the row (starting from 1).
 *
 * @returns {Float64Array} typed array with column values.
 *
 */
Matrix.prototype.getrow = function(rind) {
   const ncols = ncol(this);
   const nrows = nrow(this);
   const out = new Float64Array(ncols);
   for (let c = 0; c < ncols; c++) {
      out[c] = this.v[c * nrows + (rind - 1)];
   }
   return out;
}


/**
 * Convert matrix to a text representation for showing the values in nice way.
 *
 * @param {number} ndec - number of decimals to round the values to.
 *
 * @returns {string} the string with matrix representation.
 *
 * TODO: 1) add right side alignment, column names and row indices
 * TODO: 2) add workaround for large matrices (...)
 *
 */
Matrix.prototype.toString = function(ndec) {

   if (ndec === undefined) {
      ndec = 2;
   }

   const nvar = ncol(this);
   const ndigits = Math.round(Math.log10(max(this.v))) + ndec + 3;

   let str = "";
   const Xt = this.t();
   for (let c = 1; c <= ncol(Xt); c++) {
      str += Xt.getcolumn(c).map(v=> v.toFixed(ndec)).join("\t") + "\n"
   }

   return str;
}


/**
 * Convert matrix values to a string which can be downloaded as CSV file.
 *
 * @param {string} sep - symbol to use for separation of values (by default ",").
 * @param {string} dec - symbold to use for separation of decimals (by default ".").
 * @param {Array} header - optional array of header elements (column names).
 * @param {Array} labels - optional array of labels (row names).
 *
 * @returns {string} - string with CSV data.
 */
Matrix.prototype.toCSV = function(sep, dec, header, labels) {

   // set default separator symbol
   if (sep === undefined) {
      sep = ",";
   }

   // set default decimals separator symbol
   if (dec === undefined) {
      dec = sep === ";" ? "," : "."
   }

   const hasLabels = labels !== undefined && labels !== null && labels.length > 0;
   const hasHeader = header !== undefined && header !== null && header.length > 0;

   if (hasLabels && labels.length !== nrow(this)) {
      throw Error ("Matrix.toCSV: number of elements in labels array must match number of rows in the matrix.");
   }

   if (hasHeader && header.length !== ncol(this) + (hasLabels ? 1 : 0)) {
      throw Error ("Matrix.toCSV: number of elements in header must match number of columns in the matrix.");
   }

   // transpose to make it faster
   const m = this.t();
   let rows = hasHeader ? header.join(sep) + "\n" : "";
   for (let c = 1; c <= ncol(m); c++) {
      rows = rows + (hasLabels ? labels[c - 1] + sep : "") + m.getcolumn(c).join(sep) + "\n";
   }

   // change decimal separator if it is not a dot
   if (dec !== "." && dec !== sep) {
      rows = rows.replace(/\./g, dec);
   }

   return rows;
}



/*************************************************/
/*    Static methods for Matrix constructor      */
/*************************************************/


/**
 * Parse string with data from a CSV file and create a matrix with values.
 *
 * @param {string} strData - string with all data from the CSV file.
 * @param {string} sep - symbols used for separation of values (by default ",").
 * @param {boolean} hasHeader - logical, if first row must be treated as header or not.
 * @param {boolean} hasLabels - logical, if first column must be treated as object labels or not.
 *
 *
 * @returns {Object} JSON containing matrix with parsed values, header, values and decimal separators.
 */
Matrix.parseCSV = function(strData, sep, hasHeader, hasLabels) {


   // function which returns true if at least one value in array can not be parsed to float
   function hasNaN(vec) {
      return vec.some(v => Number.isNaN(Number.parseFloat(v.trim())));
   }

   // set default separator symbol
   if (sep === undefined) {
      sep = ",";
   }

   // setup decimal separator
   const dec = sep === ";" ? "," : ".";

   // remove BOM symbol if any
   strData = strData.replace(/^\uFEFF/gm, "").replace(/^\u00EF?\u00BB\u00BF/gm,"");

   // remove \r symbols
   strData = strData.replace(/\r/gm, "");

   // split string with data values into array of rows
   let rows = strData.split(/\n/);

   // check if the first row contains header
   let header = [];
   let startRow = 0;
   const firstRow = rows[0].split(sep);
   if (hasHeader || (hasHeader === undefined && hasNaN(firstRow))) {
      header = firstRow;
      startRow = 1;
   }

   // filter empty rows out
   rows = rows.filter(v => v.trim().length > 1);

   // check if first column contains labels
   let labels = [];
   let startColumn = 0;
   if (hasLabels) {
      startColumn = 1;
   }

   // number of rows and columns in future matrix
   const nRows = rows.length - startRow;
   const nCols = firstRow.length - startColumn;


   if (nRows < 1) {
      throw Error("Data file should contain at least 1 row with values.");
   }

   if (nCols < 1) {
      throw Error("Data file should contain at least 1 column with values.");
   }

   // set rows of data as columns of matrix - it is faster
   const data = zeros(nCols, nRows);

   // find symbol used for decimals separator
   for (let r = startRow; r < nRows + startRow; r++) {

      // parsing: trim spaces and replace non-numbers with dot (.)
      const rowValuesAll = rows[r].split(sep)

      let rowValues;
      if (hasLabels) {
         labels[r - startRow] = rowValuesAll[0];
         rowValues = rowValuesAll.slice(1).map(v => Number.parseFloat(v.trim().replace(dec, ".")));
      } else {
         rowValues = rowValuesAll.map(v => Number.parseFloat(v.trim().replace(dec, ".")));
      }

      if (rowValues.some(v => Number.isNaN(v))) {
         throw Error("Some of the values can not be converted to numbers, check your CSV file and try again.");
      }

      if (rowValues.length != nCols) {
         throw Error("Wrong number of values in row #" + r);
      }

      data.v.set(new Float64Array(rowValues), (r - startRow) * nCols);
   }

   return {values: data.t(), header: header, labels: labels, sep:sep, dec:dec};
}
