<script>

   import AppPlotContainer from './AppPlotContainer.svelte';

   import DataPlotControls from './mdatools-plots/DataPlotControls.svelte';
   import DataPlot from './mdatools-plots/DataPlot.svelte';

   import DistancePlotControls from './mdatools-plots/DIstancePlotControls.svelte';
   import DistancePlot from './mdatools-plots/DistancePlot.svelte';

   import RMSEPlotControls from './mdatools-plots/RMSEPlotControls.svelte';
   import RMSEPlot from './mdatools-plots/RMSEPlot.svelte';

   import ScalarsPlotControls from './ScalarsPlotControls.svelte';
   import ScalarsPlot from './ScalarsPlot.svelte';

   import { splitregdata } from './mdatools/reg';

   export let dataset;
   export let model;
   export let results;
   export let pvset;
   export let scalars;

   let plot = "scalars";
   let settings = {};

   function switchPlot(name) {
      settings = {};
      plot = name;
   }

   $: [X, Y] = splitregdata(dataset.values);
   $: [Xpv, Ypv] = splitregdata(pvset);
</script>

<AppPlotContainer>

   <div style="height: 100%" slot="plot-area">
      {#if plot === "distance"}
      <DistancePlot {model} {settings} {results} />
      {:else if plot === "data"}
      <DataPlot datasets={[X, Xpv]} />
      {:else if plot === "scalars"}
      <ScalarsPlot {scalars} />
      {:else if plot === "rmse"}
      <RMSEPlot {model} {results} />
      {/if}
   </div>

   <div slot="plot-controls">

      <!-- controls for scalars plot -->
      {#if plot === "scalars"}
      <ScalarsPlotControls/>
      {:else}
      <div
         class="plot-selector"
         on:keypress={() => switchPlot("scalars")}
         on:click={() => switchPlot("scalars")}>
         Scalars c<sub>k</sub>/c
      </div>
      {/if}


      <!-- controls for data plot -->
      {#if plot === "data"}
      <DataPlotControls/>
      {:else}
      <div
         class="plot-selector"
         on:keypress={() => switchPlot("data")}
         on:click={() => switchPlot("data")}>
         Data
      </div>
      {/if}

      <!-- controls for RMSE plot -->
      {#if plot === "rmse"}
      <RMSEPlotControls/>
      {:else}
      <div
         class="plot-selector"
         on:keypress={() => switchPlot("rmse")}
         on:click={() => switchPlot("rmse")}>
         RMSE
      </div>
      {/if}


      <!-- controls for distance plot -->
      {#if plot === "distance"}
      <DistancePlotControls {model} bind:settings={settings}/>
      {:else}
      <div
         class="plot-selector"
         on:keypress={() => switchPlot("distance")}
         on:click={() => switchPlot("distance")}>
         Distances
      </div>
      {/if}

   </div>
</AppPlotContainer>


<style>
</style>
