<script>
   import { pcrfit, pcrpredict } from './mdatools/pcr.js';
   import { splitregdata } from './mdatools/reg.js';
   import { pcvpcr } from './mdatools/pcv.js';
   import { cbind } from './mdatools/Matrix.js';

   import AppContainer from './AppContainer.svelte';
   import AppPlots from './RegAppPlots.svelte';

   // wrappers for PCR methods
   function apppcrfit(data, ncomp, center, scale) {
      const [X, Y] = splitregdata(data);
      return pcrfit(X, Y, ncomp, center, scale);
   }

   function apppcrpredict(model, data, name) {
      const [X, Y] = splitregdata(data);
      return pcrpredict(model, X, Y, name);
   }

   function apppcvpcr(data, model, ncomp, cv) {
      const [X, Y] = splitregdata(data);
      const [Xpv, D] = pcvpcr(X, Y, model, ncomp, cv);
      return [cbind(Y, Xpv), D];
   }

   const methods = {
      predict: apppcrpredict,
      fit: apppcrfit,
      pcv: apppcvpcr
   }
</script>

<AppContainer methodName="PCR" plotComponent={AppPlots} {methods} />
