<script>
   import { getContext } from 'svelte';
   import { Colors } from 'svelte-plots-basic';
   import { ncol, ismatrix, nrow } from '../mdatools/Matrix.js';
   import { isvector } from '../mdatools/vectors.js';
   import { mrange } from '../mdatools/stat.js';

   // input parameters
	export let xValues;
   export let yValues;
   export let title = "";
   export let lineWidth = 1;
   export let lineColor = Colors.PRIMARY;
   export let lineType = 1;


   // function to turn values to text coordinates for a polygon
   function val2p(x, y, isOk, xLim, yLim, axesWidth, axesHeight) {
      if (!isOk || x === undefined || y === undefined) return undefined;

      const px = axes.scaleX(x, xLim, axesWidth);
      let p = [];
      for (let c = 1; c <= ncol(y); c++) {
         const py = axes.scaleY(y.getcolumn(c), yLim, axesHeight)
         let lp = "";
         for (let i = 0; i < x.length; i++) {
            lp += px[i] + "," + py[i] + " ";
         }
         p[c - 1] = lp;
      }

      return p;
   }

   // variable for coordiantes
   let pg = [];

   // get axes context and scale
   const axes = getContext('axes');
   const scale = axes.scale;
   const xLim = axes.xLim;
   const yLim = axes.yLim;
   const axesWidth = axes.width;
   const axesHeight = axes.height;
   const isOk = axes.isOk;

   // sanity checks for and adjustments for x and y values
   $: {
      if (!ismatrix(yValues)) {
         throw Error("LineGroupSeries: parameter 'yValues' must be a matrix.");
      }

      if (!isvector(xValues)) {
         throw Error("LineGroupSeries: parameter 'xValues' must be a vector.");
      }

      if (xValues.length !== nrow(yValues)) {
         throw Error("LineGroupSeries: parameter 'xValues' must have the same number of values as number of rows in 'yValues'.");
      }

      // // compute ranges for x and y values
      const xValuesRange = mrange(xValues, 0.05);
      const yValuesRange = mrange(yValues.v, 0.05);

      axes.adjustXAxisLimits(xValuesRange);
      axes.adjustYAxisLimits(yValuesRange);
   }

   // reactive variable for coordinates of data points in pixels
   $: pg = val2p(xValues, yValues, $isOk, $xLim, $yLim, $axesWidth, $axesHeight);

   // reactive variable for line styles
   $: lineStyleStr = `fill:transparent;stroke:${lineColor};stroke-width: ${lineWidth}px;
      stroke-dasharray:${axes.LINE_STYLES[$scale][lineType - 1]}`;

</script>

{#if pg !== undefined && pg.length > 0}
   <g class="series series_line" style={lineStyleStr} title={title}>
      {#each pg as p}
      <polyline class="line" points={p}/>
      {/each}
   </g>
{/if}

