<script>
   import DataLoader from './DataLoader.svelte';
   import PCVCalculator from './PCVCalculator.svelte';
   import ModelCalculator from './ModelCalculator.svelte';

   export let plotComponent;
   export let methods;
   export let methodName;

   let dataset = null;
   let pvset = null;
   let model = null;
   let scalars = null;

   $: results = (dataset !== null && pvset !== null && model !== null) ?
         [  methods.predict(model, dataset.values, "cal"),
            methods.predict(model, pvset, "pv")] : null;

</script>

<div class="app-container">

   <div class="app-controls">
      <div class="data-loader">
         <DataLoader bind:dataset={dataset} />
      </div>
      <div class="model-calculator">
         <ModelCalculator {dataset} fitfunction={methods.fit} bind:model={model} />
      </div>
      <div class="pcv-calculator">
         <PCVCalculator {dataset} {model} pcvfunction={methods.pcv} bind:scalars={scalars} bind:pvset={pvset} />
      </div>
   </div>

   <div class="app-content">
      {#if !pvset || !results}
         <div class="help-text">

         {#if dataset === null}
            <p>
               <strong>Step 1.</strong>
               Load your calibration set from a CSV file. The file should contain numeric values delimited
               by comma (with dot as decimal separator) or by semicolon (with comma as decimal separator). Rows correspond to observations or objects, columns  to variables. First row can contain variable names (header) and first column can contain labels for the objects/observations just set the corresponding options after selecting
               the file.
               {#if methodName !== "PCA"}
               <span>First column in the data set will be treated as response variable (y).</span>
               {/if}
            </p>
         {:else}

            {#if model === null}
            <p>
               <strong>Step 2</strong>
               Next step is to create a global model for the training set. Use more components/latent variables (LVs)
               than you expect to be optimal. In case of PCA you can use as many components as possible.
            </p>
            {/if}


            {#if model !== null && pvset === null}
            <p>
               <strong>Step 3</strong>
               Now set necessary parameters for Procrustes cross-validation method and click button to compute the PV-set. It is recommended to use fewer number of segments (e.g. 3 or 4). The more segment you use the more time it will take to compute the PV-set.
            </p>
            {/if}
         {/if}
      </div>
      {:else}
         {#key results}
         <svelte:component this={plotComponent} {model} {results} {dataset} {pvset} {scalars} />
         {/key}
      {/if}
   </div>
</div>

