<script>
// Matrix and related methods
import { nrow, ncol, Matrix } from './mdatools/Matrix.js';
import { mean } from './mdatools/stat.js';
import { rsvd } from './mdatools/decomp.js';

// control elements
import FileSelector from './controls/FileSelector.svelte';
import Select from './controls/Select.svelte';
import ControlElementContainer from './controls/Container.svelte';
import ButtonCancel from './controls/ButtonCancel.svelte';
import Button from './controls/Button.svelte';

// calibration set
export let dataset = null;

let x = null;
let dataFile = null;
let sep = ",";
let firstColumn = "values";
let firstRow = "header"

let status = "neutral";
let errMsg = "";

// possible delimeters
const sepOptions = [",", ";"];
const firstRowOptions = ["header", "values"];
const firstColumnOptions = ["labels", "values"];

// gets CSV content as one large string, parse it into matrix
function setData(strData) {
   try{
      dataset = Matrix.parseCSV(
         strData,
         sep,
         firstRow === "header",
         firstColumn === "labels"
      );
      errMsg = ""
      status = "success";
   } catch (e) {
      dataset = null;
      status = "error";
      errMsg = e;
   }
}

// promise for reading the CSV file
function readFile (e) {
   const reader = new FileReader()
   reader.onload = () => setData(reader.result);
   reader.readAsBinaryString(dataFile)
}

// reset whole component to initial state
function reset () {
   dataset = null;
   dataFile = null;
}

// reactive expression for tracking changes of 'dataFile'
$: if (dataFile === null) {
   // if data file is empty reset status and error messages
   reset();
} else {
   // if not just remove error messages if any
   status = "neutral";
   errMsg = "";
}

// reactive expression for tracking changes of 'sep'
$: if (sep) {
   // if separator was changed remove error message
   status = "neutral";
   errMsg = "";
}

</script>

{#if dataset === null}

<!-- show if data is not loaded -->
<!-- reset component if dataFile is changed inside -->
{#key dataFile === undefined}
<ControlElementContainer>
   <FileSelector id="dataFile" bind:file={dataFile} />
</ControlElementContainer>
{/key}

<!-- show if file is selected but data is not loaded -->
{#if dataFile}
<ControlElementContainer label="Delimiter:">
   <Select id="sep" bind:value={sep} options={sepOptions} />
</ControlElementContainer>
<ControlElementContainer label="First row:">
   <Select id="firstRow" bind:value={firstRow} options={firstRowOptions} />
</ControlElementContainer>
<ControlElementContainer label="First column:">
   <Select id="firstColumn" bind:value={firstColumn} options={firstColumnOptions} />
</ControlElementContainer>
<ControlElementContainer label="&nbsp;">
   <Button on:click={readFile}>Load</Button>
</ControlElementContainer>
{/if}

{:else}

<!-- dataset is loaded - show info and cancel button -->
<ControlElementContainer>
<p>Dataset is loaded: {nrow(dataset.values)} &times; {ncol(dataset.values)},
   {dataset.header.length === 0 ? "no header" : "with header"}</p>
   <ButtonCancel on:click={reset} />
</ControlElementContainer>
{/if}

<!-- error message -->
{#if status === "error" || errMsg}
<ControlElementContainer status="error">
   <p>{errMsg}</p>
</ControlElementContainer>
{/if}


<style>
</style>