<script>
   import { plsfit, plspredict } from './mdatools/pls.js';
   import { splitregdata } from './mdatools/reg.js';
   import { pcvpls } from './mdatools/pcv.js';
   import { cbind } from './mdatools/Matrix.js';

   import AppContainer from './AppContainer.svelte';
   import AppPlots from './RegAppPlots.svelte';

   // wrappers for PLS methods
   function appplsfit(data, ncomp, center, scale) {
      const [X, Y] = splitregdata(data);
      return plsfit(X, Y, ncomp, center, scale);
   }

   function appplspredict(model, data, name) {
      const [X, Y] = splitregdata(data);
      return plspredict(model, X, Y, name);
   }

   function apppcvpls(data, model, ncomp, cv) {
      const [X, Y] = splitregdata(data);
      const [Xpv, D] = pcvpls(X, Y, model, ncomp, cv);
      return [cbind(Y, Xpv), D];
   }

   const methods = {
      predict: appplspredict,
      fit: appplsfit,
      pcv: apppcvpls
   }
</script>

<AppContainer methodName="PLS" plotComponent={AppPlots} {methods} />
