/************************************************************/
/*    Methods for generating and manipulation of vectors    */
/************************************************************/

/**
 * Return true if x is a vector (normal or typed array)
 */
export function isvector(x) {
   return Array.isArray(x) || ArrayBuffer.isView(x);
}

/**
 * Create a subset of a vector using vector with indices.
 *
 * @param {Float64Array} x - vector with values.
 * @param {Int32Array} ind - vector with indices.
 */
export function subsetv(x, ind) {

   const n = ind.length;
   const out = new Float64Array(n);
   for (let i = 0; i < n; i++) {
      out[i] = x[ind[i] - 1];
   }

   return out;
}

/**
 * Generate sequence of values - a vector with values between 'start' and 'end'.
 *
 * @param {number} start - first value.
 * @param {number} end - last value.
 * @param {number} by - increment between values.
 * @param {bulean} int - logical, shall sequence values be integer (default) or float.
 *
 * @returns {Float64Array|Int32Array} vector with sequence values.
 *
 */
export function seq(start, end, by, int) {

   // by default return integer values
   if (int === undefined) int = true;

   // by default increment is unit
   if (by === undefined) by = (start <= end) ? 1 : -1;

   const length = Math.floor((end - start) / by) + 1;

   if (length < 1) {
      throw Error('seq: wrong combination of parameters.');
   }

   const constructor = int ? Int32Array : Float64Array;
   const x = new constructor(length);
   for (let i = 0; i < length; i++) {
      x[i] = start + i * by;
   }

   return x;
}


/**
 * Replicate a number or a vector of numbers 'n' times.
 *
 * @param {number | number[]} x - a number of a vector of numbers to replicate.
 * @param {number} n - how many times 'x' must be replicated.
 *
 * @returns {Float64Array} a vector with replicated 'x' values.
 *
 */
export function rep(x, n) {

   if (n < 1) {
      throw Error('rep: parameter "n" must be an integer number > 1.');
   }

   if (typeof(x) === 'number') {
      const out = new Float64Array(n);
      for (let i = 1; i <= n; i++) {
         out[i - 1] = x;
      }
      return out;
   }

   if (x.length === undefined || x.length < 1) {
      throw Error('rep: parameter "x" must be a number or a vector with numbers.');
   }

   const l = x.length * n;
   const out = new x.constructor(l);
   for (let i = 1; i <= n; i++) {
      for (let j = 0; j < x.length; j++) {
         out[(i - 1) * x.length + j] = x[j];
      }
   }

   return out;
}


/**
 * Replicate each value in a vector 'x' 'n' times.
 *
 * @param {number[]} x - a vector of numbers to replicate.
 * @param {number} n - how many times each value in 'x' must be replicated.
 *
 * @returns {Float64Array} a vector with replicated values.
 *
 */
export function repeach(x, n) {


   if (n < 1) {
      throw Error('rep: parameter "n" must be an integer number > 1.');
   }

   if (typeof(x) === 'number') {
      return rep(x, n);
   }

   if (x.length === undefined || x.length < 1) {
      throw Error('rep: parameter "x" must be a vector with numbers.');
   }

   const l = x.length * n;
   const out = new x.constructor(l);
   for (let i = 0; i < x.length; i++) {
      for (let j = 1; j <= n; j++) {
         out[i * n + j - 1] = x[i];
      }
   }

   return out;
}

