/********************************************************************/
/*    Methods for Preprocessing (work with vectors and matrices)    */
/********************************************************************/

import { ncol, nrow, zeros } from './Matrix.js';
import { mean, sd } from './stat.js';
import { rep } from './vectors.js';


/**
 * Uncenter and unscale values in every column of a matrix.
 *
 * @param {Matrix} X - matrix with values.
 * @param {Float64Array} center - vector with numbers to use for uncentering.
 * @param {Float64Array} scale - vector with numbers to use for unscaling.
 *
 * @returns {Float64Array} a matrix with uncentered and unscaled column values.
 *
 */
export function unscale(X, centerValues, scaleValues) {

   const nrows = nrow(X);
   const ncols = ncol(X);


   const Xp = zeros(nrows, ncols);
   for (let c = 1; c <= ncols; c++) {
      const xc = X.getcolumn(c);
      const xpc = Xp.getcolumn(c);

      const cv = centerValues[c - 1];
      const sv = scaleValues[c - 1];

      for (let r = 0; r < nrows; r++) {
         xpc[r] = xc[r] * sv + cv;
      }
   }

   return Xp;
}

/**
 * Center and scale values in every column of a matrix.
 *
 * @param {Matrix} X - matrix with values.
 * @param {boolean|Float64Array} [center=true] - logical value or a vector with numbers to use for centering.
 * @param {boolean|Float64Array} [scale=true] - logical value or a vector with numbers to use for scaling.
 * @param {boolean} [full=true] - logical, influences the return values.
 *
 * @returns {Array|Float64Array} either a matrix with centered and scaled column values (if 'fool = false')
 * or an array with three elements: the matrix, vector with values for centring and vector with values
 * for scaling.
 *
 */
export function scale(X, center, scale, full) {

   if (center === undefined) {
      center = true;
   }

   if (scale === undefined) {
      scale = false;
   }

   if (full === undefined) {
      full = false;
   }

   const nrows = nrow(X);
   const ncols = ncol(X);

   function getStat(X, param, fun, alt) {

      if (typeof(param) === "boolean") {
         return param ? X.apply(fun, 2) : rep(alt, ncols);
      }

      if (typeof(param) !== "object") {
         throw Error("scale: parameters 'center' and 'scale' must be boolean or array with numeric values.");
      }

      if (param.length !== ncols ) {
         throw Error("scale: number of values for centring and scaling must be the same as number of columns in the matrix.");
      }

      return param;
   }

   // prepare values for centering and scaling
   const centerValues = getStat(X, center, mean, 0);
   const scaleValues = getStat(X, scale, sd, 1);

   const Xp = zeros(nrows, ncols);
   for (let c = 1; c <= ncols; c++) {
      const xc = X.getcolumn(c);
      const xpc = Xp.getcolumn(c);

      const cv = centerValues[c - 1];
      const sv = 1 / scaleValues[c - 1];

      for (let r = 0; r < nrows; r++) {
         xpc[r] = (xc[r] - cv) * sv;
      }
   }

   return full ? [Xp, centerValues, scaleValues] : Xp;
}

