/************************************************************/
/*    Methods for Principal Component Analysis              */
/************************************************************/

import { seq } from './vectors.js';
import { diagm, tcrossprod, ncol, nrow, dot, zeros, rowSums } from './Matrix.js';
import { ssq, mean, sd, sum } from './stat.js';
import { rsvd } from './decomp.js';
import { scale as prep_scale } from './prep.js';


export function pcafit(X, ncomp, center, scale) {

   if (center === undefined) {
      center = true;
   }

   if (scale === undefined) {
      scale = false;
   }

   if (!ncomp) {
      ncomp = Math.min(ncol(X), nrow(X) - 1);
   }

   // center and scale the training set
   const [Xp, mX, sX] = prep_scale(X, center, scale, true);

   // compute loadings and eigenvalues
   const m = rsvd(Xp, ncomp);
   const eigenvals = m.s.map(x => x * x / (nrow(X) - 1));

   // compute main PCA results for calibration set
   const calres = pcagetmainres(Xp, dot(m.U, diagm(m.s)), m.V, eigenvals);

   // compute mean values for distances
   const [h0, Nh] = getDistParams(calres.H);
   const [q0, Nq] = getDistParams(calres.Q);

   // return the model object
   return {
      class: ["pcamodel"],
      P: m.V,
      eigenvals: eigenvals,
      center: center,
      scale: scale,
      mX: mX,
      sX: sX,
      q0: q0,
      h0: h0,
      Nq: Nq,
      Nh: Nh,
      ncomp: ncomp
   }
}

/**
 * Compute parameters of distance distributions from distance matrix U.
 *
 * @param {Matrix} U - matrix with distances.
 *
 * @returns {Array} two Float64Arrays, with u0 and Nu.
 */
export function getDistParams(U) {

   const ncomp = ncol(U);
   const u0 = U.apply(mean, 2);
   const su = U.apply(sd, 2);

   const Nu = new Float64Array(ncol(U));
   for (let i = 0; i < ncomp; i++) {
      const f = u0[i] / su[i];
      const N = Math.round(2 * f * f);
      Nu[i] = N < 1 ? 1 : N > 250 ? 250 : N;
   }

   return [u0, Nu]
}

export function pcagetmainres(Xp, T, P, eigenvals) {

   const ncomp = ncol(P);
   const U2 = T.apply(x => x * x, 0).divide(eigenvals, 2);

   const H = zeros(nrow(Xp), ncomp);
   const Q = zeros(nrow(Xp), ncomp);
   const expvar = new Float64Array(ncomp);
   const cumexpvar = new Float64Array(ncomp);

   // compute total sum of squares
   const totssq = ssq(Xp.v);

   for (let a = 1; a <= ncomp; a++) {
      const Xhat = tcrossprod(T.subset([], seq(1, a)), P.subset([], seq(1, a)));
      const E2 = Xp.subtract(Xhat).apply(x => x * x, 0);

      const q = rowSums(E2);
      H.v.set(rowSums(U2.subset([], seq(1, a))), (a - 1) * nrow(Xp));
      Q.v.set(q, (a - 1) * nrow(Xp));

      cumexpvar[a - 1] = 100 * (1 - sum(q) / totssq);
      expvar[a - 1] = a === 1 ? cumexpvar[a - 1] : cumexpvar[a - 1] - cumexpvar[a - 2];
   }

   return {
      class: ["pcares", "ldecomp"],
      T: T,
      H: H,
      Q: Q,
      expvar: expvar,
      cumexpvar: cumexpvar
   }
}

export function pcapredict(m, X, name) {

   const Xp = prep_scale(X, m.mX, m.sX);
   let res = pcagetmainres(Xp, dot(Xp, m.P), m.P, m.eigenvals);
   res.name = name;

   return res;
}
